@import url(https://fonts.googleapis.com/css?family=Heebo:100,300,400,500,700,800,900);
body {
  margin: 0;
  margin-top: 30px;
  padding: 0;
  font-family: "Heebo", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f6f7f9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* .App {
  text-align: center;
  background-color: #f6f7f9;
} */

.category-icons {
  max-height: 30px;
}

/*** General Elements ***/

.logo {
  width: 80px;
  margin: 16px;
  padding: 0px;
}

.auth-entry {
  margin: 40px 0px;
}

/*** Typographic Elements ***/

h1 {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
}

h2 {
  font-weight: 800;
}

h4 {
  font-weight: 700;
}

h5 {
  font-weight: 500;
}

.bold-text {
  font-weight: 500;
}

.description {
  margin: 0px;
  margin-left: 0px;
  margin-right: 0px;
}

.small-text {
  font-size: 14px;
  color: #666666;
}

.line-height {
  padding-top: 0px;
  margin-bottom: 0.5rem;
}

/*** Form Elements ***/
label {
  display: -webkit-flex;
  display: flex;
  color: #333333;
  font-size: 14px;
  margin-bottom: 0.25rem;
}

input {
  border: 1px solid #c2c5c4;
  border-radius: 2px;
  width: 100%;
  padding: 4px;
  margin-bottom: 0.5rem;
}

.contain-center {
  text-align: center;
  width: 370px;
  padding: 1.5rem;
  padding-bottom: 0.5rem;
}

/*** Interactive Elements ***/

.text-link {
  font-size: 14px;
  font-weight: bold;
  color: #3d90de;
  text-decoration: none;
}

.primary-btn {
  color: white;
  font-size: 16px;
  font-weight: 800;
  background-image: -webkit-linear-gradient(top left, #3d90de, #7ae0eb);
  background-image: linear-gradient(to bottom right, #3d90de, #7ae0eb);
  width: 220px;
  height: 50px;
  border: none;
  margin: 1rem;
  border-radius: 3px;
}

.col-sm-6 {
  width: 50% !important;
}

/*** Bottom Navbar ***/

nav {
  position: fixed;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  padding: 10px 0px;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  background-color: white;
}

.icon {
  height: 30px;
  padding-top: 0px;
}

.App nav a.active {
  -webkit-filter: brightness(0.5) sepia(11) hue-rotate(177deg) saturate(31);
          filter: brightness(0.5) sepia(11) hue-rotate(177deg) saturate(31);
}

.boxshadow {
  position: fixed;
  box-shadow: 1px 0px 12px rgba(0, 0, 0, 0.1);
}

.tab-nav {
  width: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

/*** Mission Pages ***/

.charity-logo {
  padding-top: 0px;
  width: 100%;
  height: 150px;
  object-fit: contain;
  margin-bottom: 15px;
}

.mission-name {
  margin: 30px 0px;
}

.mission-card {
  border: 1px solid #d6d6d6;
  border-radius: 3px;
  background-color: white;
  padding: 30px 20px;
  margin: 15px 0;
  box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.1);
}

.mission-card-btn {
  text-align: center;
}

.search-btn {
  color: white;
  background-color: #3d90de;
  border-radius: 2px;
  border: none;
  padding: 0px 16px;
  margin: 0px;
  margin-left: 10px;
}

.marginBottom {
  margin-bottom: 70px;
}

.addMargins {
  border: 1px solid #d6d6d6;
  border-radius: 3px;
  background-color: white;
  padding: 30px 20px;
  margin: 15px 0;
  box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.1);
  height: 500px;
}

.descriptionSize {
  height: 35px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.charity-logo-2 {
  padding-top: 0px;
  width: 80%;
  height: 150px;
  object-fit: contain;
  margin-bottom: 15px;
}

.marginSides {
  margin: 0 10px;
}

/*** Dashboard Page ***/

.category-btn {
  color: white;
  font-size: 16px;
  font-weight: 800;
  background-image: -webkit-linear-gradient(top left, #3d90de, #7ae0eb);
  background-image: linear-gradient(to bottom right, #3d90de, #7ae0eb);
  width: 160px;
  height: 50px;
  border: none;
  margin: 16px 8px;
  border-radius: 3px;
}

.dashboard-bg {
  /* border: 1px solid #d6d6d6;
  border-radius: 3px;
  background-color: white; */
  padding: 30px 20px;
  margin: 15px 0;
  /* -moz-box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.1);
  box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.1); */
}

.list {
  background-color: lightgray;
  padding: 10px;
  margin-bottom: 30px;
}

.list img {
  width: 50px;
  height: 50px;
  object-fit: contain;
  padding: 0;
  margin: 10px 0;
}

.leftside {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  text-align: left;
  -webkit-align-items: center;
          align-items: center;
}

.rightside {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  text-align: right;
  -webkit-align-items: center;
          align-items: center;
}

.rightside h2 {
  margin: 0;
}

.description {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: end;
          align-items: end;
  margin: 0 20px;
}

.description p {
  margin: 0;
  padding-top: 0;
}




.category-icons {
  max-height: 30px;
}
.icon-borders {
  border: 2px;
}

ul {
  list-style-type: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

button {
  margin: 10px;
  margin-right: auto;
  margin-left: auto;
}

.item-logo {
  max-height: 25px;
}

img {
  padding-top: 10px;
}

p {
  padding-top: 10px;
}



.logo-details {
  max-height: 200px;
}


* {
  padding: 0;
  margin: 0;
}

.custom {
  width: 40px;
  height: 50px;
  color: black;
}

.input-style {
  margin-top: 10px;
  border-color: transparent;
  box-shadow: none;
  background-color: transparent;
  border-bottom: 2px solid blue;
  border-radius: 0;
  width: 200px;
  text-align: left;
}

.h3-add-category {
  margin-top: 30px;
  margin-bottom: 30px;
}

